@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #000;
  font-family: "Poppins", sans-serif;
}
.home {
  position: relative;
  width: 100%;
  min-height: 100vh;
  padding: 100px;
  /* background: url(); background-size: cover; USE THIS FOR PICTURE AS BACKGROUND*/
  display: flex;
  align-items: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.content {
  width: 100%;
}
.content h2 {
  color: #fff;
  font-size: 2.5em;
}
#name {
  color: #40c3ff;
  font-size: 1.2em;
}
.content .buttons {
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cvButton {
  display: flex;
  align-items: center;
  justify-content: center;
}

#wrap {
  border-right: 0.08em solid #fff;
}
.typewrite {
  text-align: center;
}

.line-1 {
  position: relative;
  top: 50%;
  margin: 0 auto;
  border-right: 2px solid rgba(255, 255, 255, 0.75);
  white-space: nowrap;
  overflow: hidden;
  transform: translateY(-50%);
}

/* Animation */
.anim-typewriter {
  -webkit-animation: blinkTextCursor 500ms steps(44) infinite normal;
          animation: blinkTextCursor 500ms steps(44) infinite normal;
}
@-webkit-keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
    border-right-color: transparent;
  }
}
@keyframes blinkTextCursor {
  from {
    border-right-color: rgba(255, 255, 255, 0.75);
  }
  to {
    border-right-color: transparent;
  }
}

@media (max-width: 750px) {
  header {
    padding: 20px 50px;
  }
  .banner {
    padding: 100px 50px 150px;
  }
  .banner h2 {
    font-size: 1.8em;
  }
  .banner p {
    font-size: 1em;
  }
  .banner .content p,
  .banner .content a {
    font-size: 1em;
  }
  .banner.active {
    transform: translateX(-250px);
  }

  .content .buttons {
    width: 100%;
    border: 10px;
  }
  .content .buttons .logoText {
    display: none;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}
:root {
  --main-color: coral;
}
.containerButton {
  display: inline-flex;
}
#neonButton {
  position: relative;
  padding: 10px 30px;
  margin: 20px 30px 30px 30px;
  color: coral;
  color: var(--main-color);
  text-decoration: none;
  /* text-transform: uppercase; */
  letter-spacing: 2px;
  font-size: 20px;
  overflow: hidden;
  transition: 0.5s;
  -webkit-box-reflect: below 1px linear-gradient(transparent, #0003);
}
#neonButton:hover {
  background: coral;
  background: var(--main-color);
  color: #111;
  box-shadow: 0 0 50px coral;
  box-shadow: 0 0 50px var(--main-color);
  transition-delay: 0.3s;
}
#neonButton:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  border-top: 2px solid coral;
  border-top: 2px solid var(--main-color);
  border-left: 2px solid coral;
  border-left: 2px solid var(--main-color);
  transition: 0.5s;
  transition-delay: 0.2s;
}
#neonButton:hover:before {
  width: 100%;
  height: 100%;
}
#neonButton:after {
  content: "";
  position: absolute;
  bottom: 0;
  right: 0;
  width: 10px;
  height: 10px;
  border-bottom: 2px solid coral;
  border-bottom: 2px solid var(--main-color);
  border-right: 2px solid coral;
  border-right: 2px solid var(--main-color);
  transition: 0.5s;
  transition-delay: 0.2s;
}
#neonButton:hover:after {
  width: 100%;
  height: 100%;
}

